import {
    ALERT_MESSAGE_WARNING,
    ALERT_MESSAGE_ERROR,
    ALERT_MESSAGE_CLEAR
} from "../Constants";

export const alertActions = {
    warn,
    error,
    clear
};

function warn(message) {
    // ToDo: show toastr message
    return { type: ALERT_MESSAGE_WARNING, message };
}

function error(message) {
    console.error(message);
    return { type: ALERT_MESSAGE_ERROR, message };
}

function clear() {
    return { type: ALERT_MESSAGE_CLEAR };
}



