import React, { Component } from "react";
import { connect } from "react-redux";

class Resume extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hasError: false
        };
    }

    renderExtras(tasks) {
        return tasks.map((t, i) => {
            return [
                <div className="task-title" key={i + "_title"}>
                    {t.title}
                </div>,
                <div className="task-description" key={i + "_desc"}>
                    <p>{t.description}</p>
                </div>
            ];
        });
    }

    renderExperiences(experiences) {
        if (!experiences || experiences.length < 1) return <div />;
        return experiences.map((exp, i) => {
            return (
                <div className="block" key={i}>
                    <div
                        className="block-icon"
                        style={
                            exp.logo.background
                                ? {
                                      background: exp.logo.background
                                  }
                                : {}
                        }
                    >
                        <a href={exp.url} className="block-icon">
                            <img src={exp.logo.src} alt={exp.company} />
                        </a>
                    </div>
                    <div className="block-header">
                        {exp.position} @ {exp.company}
                    </div>
                    <div className="block-sub-header">{exp.timeperiod}</div>
                    {this.renderExtras(exp.tasks)}
                </div>
            );
        });
    }

    renderEducation(education) {
        if (!education || education.length < 1) return <div />;
        return education.map((edu, i) => {
            return (
                <div className="block" key={i}>
                    <div className="block-icon">
                        <a href={edu.url} className="block-icon">
                            <img src={edu.logo.src} alt={edu.uni} />
                        </a>
                    </div>
                    <div className="block-header">
                        {edu.course}, {edu.degree} @ {edu.uni}
                    </div>
                    <div className="block-sub-header">{edu.timeperiod}</div>
                    {edu.extras && this.renderExtras(edu.extras)}
                </div>
            );
        });
    }

    renderProjects(projects) {
        if (!projects || projects.length < 1) return <div />;
        return projects.map((p, i) => {
            return (
                <div className="block" key={i}>
                    <div className="block-icon">
                        <a href={p.url} className="block-icon">
                            <img src={p.logo.src} alt={p.title} />
                        </a>
                    </div>
                    <div className="block-header">{p.title}</div>
                    <div className="block-sub-header">{p.timeperiod}</div>

                    <div className="task-title">
                        <a href={p.url}>{p.url}</a>
                    </div>
                    <div className="task-description">
                        <p>{p.description}</p>
                    </div>
                </div>
            );
        });
    }

    render() {
        let { data } = this.props;

        let exp = data.experience[0];
        let task = exp.tasks[0];

        return (
            <div>
                <div className="section-headline">Experience</div>

                {this.renderExperiences(data.experience)}

                <div className="section-headline">Education</div>

                {this.renderEducation(data.education)}

                <div className="section-headline">Projects</div>

                {this.renderProjects(data.projects)}

                <div className="section-headline" />
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        data: state.dataReducer.data
    };
}

export default connect(mapStateToProps)(Resume);



