import {
    ALERT_MESSAGE_WARNING,
    ALERT_MESSAGE_CLEAR,
    ALERT_MESSAGE_ERROR,
    DATA_LOADED,
    IS_LOADING
} from "../Constants";

export default function DataReducer(
    state = {
        loading: false,
        error: undefined,
        data: {}
    },
    action
) {
    switch (action.type) {
        // ALERT MESSAGE HANDLING
        case ALERT_MESSAGE_WARNING: {
            return {
                ...state,
                alert: {
                    message: action.message,
                    type: "WARNING"
                }
            };
        }

        case ALERT_MESSAGE_ERROR: {
            return {
                ...state,
                alert: {
                    message: action.message,
                    type: "ERROR"
                }
            };
        }

        case ALERT_MESSAGE_CLEAR: {
            return {
                ...state,
                alert: {}
            };
        }

        // DATA RETRIEVAL HANDLING
        case DATA_LOADED: {
            return {
                ...state,
                loading: false,
                data: action.data
            };
        }

        case IS_LOADING: {
            return {
                ...state,
                loading: true
            };
        }
    }

    return state;
}



