import React, { PureComponent } from "react";

class ErrorBoundary extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            hasError: false,
            info: undefined
        };
    }

    componentDidCatch(error, info) {
        this.setState({ hasError: true, error, info });

        console.error("Caught error:", error, "\nInfo:", info);
    }

    render() {
        let { hasError, info } = this.state;
        let { children, error } = this.props;

        if (!hasError) {
            return children;
        }

        return (
            <div>
                <h2>
                    <i
                        class="fa fa-fw fa-lg fa-exclamation-circle"
                        aria-hidden="true"
                    />{" "}
                    Whoops, something bad happend! :(
                </h2>
            </div>
        );
        // return (
        //     <div className="container-fluid">
        //         <div className="row align-items-center">
        //             <div className="col" />
        //             <div className="col-12 align-self-center">
        //                 <div class="card border-danger mb-3">
        //                     <h5 class="card-header text-white bg-gradient-danger">
        //                         <i
        //                             class="fa fa-fw fa-lg fa-exclamation-circle"
        //                             aria-hidden="true"
        //                         />{" "}
        //                         Whoops, something bad happend! :(
        //                     </h5>
        //                     <div class="card-body text-danger">
        //                         <div class="card-title">{error.message}</div>
        //                         <p class="card-text">{error.stack}</p>
        //                     </div>
        //                 </div>
        //             </div>

        //             <div className="col" />
        //         </div>
        //     </div>
        // );
    }
}

export default ErrorBoundary;



