import { DATA_LOADED, IS_LOADING } from "../Constants";
import { alertActions } from "./";
import { getData } from "../_helpers";
import md5 from "md5";

export const dataActions = {
    loadData
};

function loadData() {
    return dispatch => {
        dispatch(isLoading("Fetching data..."));

        let { data } = getData();

        if (data.contact) {
            let { email } = data.contact;

            data.gravatarHash = hash(email);
        }

        dispatch(finshed(data));
    };

    function isLoading(message) {
        return { type: IS_LOADING, message };
    }
    function finshed(data) {
        return { type: DATA_LOADED, data };
    }

    function hash(email) {
        return md5(email.trim().toLowerCase());
    }
}



