import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

class AboutMe extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hasError: false
        };
    }

    render() {
        let { data } = this.props;

        return (
            <div>
                <div>
                    <div className="self-summary">
                        <h1>Hello World,</h1>
                        <div className="task-description">
                            <p>{data.about}</p>
                            <p>
                                This website is based on the amazing{" "}
                                <a href="https://github.com/salomonelli/best-resume-ever">
                                    best-resume-ever
                                </a>{" "}
                                project - in particular the awesome
                                material-dark design. I re-wrote the template in{" "}
                                <a href="https://reactjs.org/">React</a>
                                {" & "}
                                <a href="https://redux.js.org/">Redux</a> as I
                                felt more comfortable with these frameworks. I'm
                                still very bad when it comes to UI in frontend
                                development, though. So it was nice to get some
                                help.
                            </p>
                        </div>
                    </div>

                    <div className="section-headline">Links</div>

                    <div className="block">
                        <Link to="/resume">Resume</Link>
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        data: state.dataReducer.data
    };
}

export default connect(mapStateToProps)(AboutMe);



