import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { dataActions } from "../_actions/data.actions";
import md5 from "md5";

class SideColumn extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            gravatarHash: undefined
        };
    }

    componentWillMount() {
        this.props.dispatch(dataActions.loadData());
    }

    Skills(skills) {
        if (!skills) return <div />;

        return skills.map((s, i) => {
            return (
                <div key={i + "_" + s.name} className="skill">
                    <span>{s.name}&nbsp;</span>
                    <div className="progressbar">
                        <div
                            className="progress-value"
                            style={{ width: s.level + "%" }}
                        />
                    </div>
                </div>
            );
        });
    }

    render() {
        let { data } = this.props;
        let { gravatarHash } = this.state;

        if (!data.contact) return <div className="left-col" />;

        return (
            <div className="left-col">
                {/* <div id="myselfpic" /> */}

                <Link to="/">
                    <img
                        id="myselfpic"
                        src={
                            "https://www.gravatar.com/avatar/" +
                            data.gravatarHash +
                            "?s=300"
                        }
                    />
                </Link>

                <div
                    className="section-headline"
                    style={{ color: "rgba(255, 255, 255, 0.53)" }}
                >
                    contact
                </div>

                {data.birth && (
                    <div className="section-item">
                        <div className="section-icon">
                            <i className="fa fa-user-circle fa-icon" />
                        </div>
                        <div className="section-text">
                            Born {data.birth.year} in {data.birth.location}
                        </div>
                    </div>
                )}

                <div className="section-item">
                    <div className="section-icon">
                        <i className="fa-icon fa fa-fort-awesome" />
                    </div>
                    <div className="section-text">
                        <ul>
                            <li>{data.contact.street}</li>
                            <li>
                                {data.contact.city}, {data.contact.country}
                            </li>
                        </ul>
                    </div>
                </div>

                <a href="tel:+491522 6028345">
                    <div className="section-item">
                        <div className="section-icon">
                            <i className="fa-icon fa fa-phone" />
                        </div>
                        <div className="section-text">{data.contact.phone}</div>
                    </div>
                </a>

                <a
                    href={
                        "mailto:" +
                        data.contact.email +
                        "?subject=Message%20Enquiry&amp;body=Hi%20" +
                        data.name.first +
                        ",%0D%0A%0D%0A"
                    }
                >
                    <div className="section-item">
                        <div className="section-icon">
                            <i className="fa-icon fa fa-envelope" />
                        </div>
                        <div className="section-text">{data.contact.email}</div>
                    </div>
                </a>

                <a href={"https://github.com/" + data.contact.github}>
                    <div className="section-item">
                        <div className="section-icon">
                            <i className="fa-icon fa fa-github" />
                        </div>
                        <div className="section-text">
                            {"github.com/" + data.contact.github}
                        </div>
                    </div>
                </a>

                <a
                    href={
                        "https://www.linkedin.com/in/" + data.contact.linkedin
                    }
                >
                    <div className="section-item">
                        <div className="section-icon">
                            <i className="fa-icon fa fa-linkedin" />
                        </div>
                        <div className="section-text">
                            {"linkedin.com/in/" + data.contact.linkedin}
                        </div>
                    </div>
                </a>

                {/* Programming Languages SEKTION */}
                <div className="section-item">
                    <div
                        className="section-headline"
                        style={{ color: "rgba(255, 255, 255, 0.53)" }}
                    >
                        Programming Languages
                    </div>
                </div>

                {this.Skills(data.progs)}

                {/* SKILLS SEKTION */}
                <div className="section-item">
                    <div
                        className="section-headline"
                        style={{ color: "rgba(255, 255, 255, 0.53)" }}
                    >
                        skills {"&"} Technologies{" "}
                    </div>
                </div>
                {this.Skills(data.skills)}

                <div className="section-item">
                    <div className="section-headline" />
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        error: state.dataReducer.error,
        data: state.dataReducer.data,
        isLoading: state.dataReducer.loading
    };
}

export default connect(mapStateToProps)(SideColumn);



